import React, { useEffect, useState } from "react";
import tw, { styled } from "twin.macro";

const ToC = ({ headings }) => {
  if (headings.length === 0) return null;
  const headingList = headings.map(item => {
    return {
      ...item,
      id: `#${item.value.replace(/\s+/g, "-").toLowerCase()}`
    };
  });
  const activeId = useActiveId(headingList);

  return (
    <Toc>
      <Title>Table of contents</Title>
      <InnerScroll>
        {headingList.map(heading => {
          if (heading.depth > 4) {
            return <div />;
          }
          return (
            <ToCElement key={heading.id}>
              <ToCLink
                href={heading.id}
                isActive={`#${activeId}` === heading.id}>
                {heading.value}
              </ToCLink>
            </ToCElement>
          );
        })}
      </InnerScroll>
    </Toc>
  );
};

function useActiveId(headingList) {
  const [activeId, setActiveId] = useState(``);

  useEffect(() => {
    const observer = new IntersectionObserver(
      entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            setActiveId(entry.target.id);
          }
        });
      },
      { rootMargin: `0% 0% -80% 0%` }
    );

    headingList.forEach(item => {
      const target = document.querySelector(item.id);
      if (!target) return;
      observer.observe(target);
    });

    return () => {
      headingList.forEach(item => {
        const target = document.querySelector(item.id);
        if (!target) return;
        observer.unobserve(target);
      });
    };
  }, [headingList]);

  return activeId;
}

const Toc = styled.ul`
  ${tw`bg-white fixed hidden lg:flex flex-col rounded p-3 my-3`};
  width: 20rem;
  left: calc(50% + 400px);
  top: 50px;
`;

const Title = tw.h2`text-2xl mb-2`;

const ToCElement = tw.li`p-1 leading-5 ml-4 mb-4 mr-4 leading-3 list-none`;

const ToCLink = styled.a(({ isActive }) => [
  tw`no-underline text-current`,
  tw`hover:text-purple-400 transition duration-300`,
  isActive && tw`no-underline text-purple-400 transition duration-300 rounded-sm`
]);

const InnerScroll = styled.div`
  scrollbar-width: thin;
  scrollbar-color: #367ee9 rgba(48, 113, 209, 0.3);
  overflow: hidden auto;
`;

export default ToC;
